  box-shadow: 0 1px 8px var(--shadow-color-1);
  color: var(--text-color);
  background: url("Search.ccd3c07b.svg") 25px no-repeat;
  border-radius: 3px;
  width: 100%;
  height: 70px;
  margin-top: 20px;
  padding: 23px 0 23px 62px;
  font-family: Avenir, sans-serif;
  font-size: 16px;
  display: block;
}

.form__large-searchbar::placeholder, .form__small-searchbar::placeholder {
  color: var(--text-color);
}

.form__large-searchbar:hover {
  box-shadow: 0 4px 4px var(--text-shadow-color), 0 3px 8px var(--shadow-color-2);
}

.form__large-searchbar:focus {
  box-shadow: 0 4px 4px var(--text-shadow-color);
  text-shadow: 0 4px 4px var(--text-shadow-color);
  background-color: #0000;
  background-image: url("Search.ccd3c07b.svg"), linear-gradient(#fff 0%, #f6f6f7 100%);
  background-position: 25px;
  background-repeat: no-repeat, repeat;
  background-size: auto, auto;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  outline: none;
  font-weight: bold;
}

.form__small-searchbar {
  border: 1px solid var(--border-color);
  box-shadow: 0 1px 8px var(--shadow-color-1);
  color: var(--text-color);
  background: url("Search.ccd3c07b.svg") 13px / 11px no-repeat;
  border-radius: 3px;
  width: 100%;
  height: 42px;
  margin-top: 20px;
  padding: 11px 0 11px 33px;
  font-family: Avenir, sans-serif;
  font-size: 14px;
  display: block;
}

.form__small-searchbar:hover {
  text-shadow: none;
  box-shadow: 0 3px 8px 0 var(--shadow-color-2);
}

.form__small-searchbar:focus {
  text-shadow: none;
  box-shadow: none;
  background-color: #0000;
  background-image: url("Search.ccd3c07b.svg"), linear-gradient(#fff 0%, #f6f6f7 100%);
  background-position: 13px, 0 0;
